const ERROR_CODE = [-5001, -5002, -5003, -5004, -5005, -5006];

export default {
    data() {
        return {
            title: '激励视频广告',
            loading: false,
            loadError: false
        }
    },
    onReady() {






        this.adOption = {
            adUnitId: ''
        };

        this.createAd();
    },
    methods: {
        createAd() {
            var rewardedVideoAd = this.rewardedVideoAd = uni.createRewardedVideoAd(this.adOption);
            rewardedVideoAd.onLoad(() => {
                this.loading = false;
                this.loadError = false;
                console.log('onLoad event')
            });
            rewardedVideoAd.onClose((res) => {
                this.loading = true;
                // 用户点击了【关闭广告】按钮
                if (res && res.isEnded) {
                    // 正常播放结束
                    console.log("onClose " + res.isEnded);
                } else {
                    // 播放中途退出
                    console.log("onClose " + res.isEnded);
                }

                setTimeout(() => {
                    uni.showToast({
                        title: "激励视频" + (res.isEnded ? "成功" : "未") + "播放完毕",
                        duration: 10000,
                        position: 'bottom'
                    })
                }, 500)
            });
            rewardedVideoAd.onError((err) => {
                this.loading = false;
                if (err.code && ERROR_CODE.indexOf(err.code) !== -1) {
                    this.loadError = true;
                }
                console.log('onError event', err)
            });
            this.loading = true;
        },
        show() {
            const rewardedVideoAd = this.rewardedVideoAd;
            rewardedVideoAd.show().catch(() => {
                rewardedVideoAd.load()
                    .then(() => rewardedVideoAd.show())
                    .catch(err => {
                        console.log('激励视频 广告显示失败', err)
                        uni.showToast({
                            title: err.errMsg || err.message,
                            duration: 5000,
                            position: 'bottom'
                        })
                    })
            })
        },
        reloadAd() {
            this.loading = true;
            this.rewardedVideoAd.load();
        }
    }
}
